"use server"

import { createServerClient } from "@/lib/supabase/server-client"
import { redirect } from "next/navigation"

/**
 * Server action to handle plan selection
 * Creates a Mercado Pago preference and redirects to checkout
 */
export async function selectPlan(planId: string) {
  const supabase = await createServerClient()

  const {
    data: { user },
  } = await supabase.auth.getUser()

  if (!user) {
    redirect("/login")
  }

  try {
    const response = await fetch(`${process.env.NEXT_PUBLIC_APP_URL}/api/mercadopago/create-preference`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ planType: planId }),
    })

    const text = await response.text() // Captura respuesta cruda
    if (!response.ok) {
      const text = await response.text()
      console.error("[v0] Mercado Pago API response (raw):", text)
      throw new Error("Error creating payment preference")
    }


    const data = JSON.parse(text)

    if (data.init_point) {
      redirect(data.init_point)
    }

    return { success: true, message: "Redirigiendo a Mercado Pago..." }
  } catch (error) {
    console.error("[v0] Error in selectPlan:", error)
    return { success: false, message: "Error al crear la preferencia de pago" }
  }
}

/**
 * Server action to purchase a pack
 * Creates a Mercado Pago preference for pack purchase
 */
export async function purchasePack(packId: string, discountCode?: string) {
  const supabase = await createServerClient()

  const {
    data: { user },
  } = await supabase.auth.getUser()

  if (!user) {
    redirect("/login")
  }

  try {
    const response = await fetch(`${process.env.NEXT_PUBLIC_APP_URL}/api/mercadopago/create-preference`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        packId,
        discountCode: discountCode || null,
      }),
    })

    const text = await response.text() // Captura respuesta cruda
    if (!response.ok) {
      console.error("[v0] Mercado Pago API response:", text)
      throw new Error("Error creating payment preference")
    }

    const data = JSON.parse(text)

    if (data.init_point) {
      redirect(data.init_point)
    }

    return { success: true, message: "Redirigiendo a Mercado Pago..." }
  } catch (error) {
    console.error("[v0] Error in purchasePack:", error)
    return { success: false, message: "Error al crear la preferencia de pago" }
  }
}
